<template>

    <!--    <v-col cols="12" md="6">-->
    <v-col cols="12" class="px-7" >
        <v-card class="mx-5">
            <!--            <v-system-bar></v-system-bar>-->
            <!--            <v-layout wrap align-content-space-around text-xs-center>-->
            <v-layout wrap align-center>
<!--                <v-flex xs3 v-for="item in saju" :key="item.message">-->
                <v-flex xs3 v-for="(item, index) in saju" :key="`item-${index}`">
                    <v-card class="m-1 rounded-card" align-center>
                        <v-responsive :aspect-ratio="1/1">
                            <v-container fill-height>
                                <v-row align="center" justify="center">
                                    <v-col class="text-center">{{item}}</v-col>
                                </v-row>
                            </v-container>
                        </v-responsive>
                    </v-card>
                </v-flex>
<!--                <v-flex xs3>-->
<!--                    <v-card color="blue">-->
<!--                        <v-card-text class="px-0">4</v-card-text>-->
<!--                    </v-card>-->
<!--                </v-flex>-->
            </v-layout>
        </v-card>
    </v-col>
</template>

<script>
    export default {
        name: "Saju",
        props: ['value'],
        data: () => ({
            // cards: [
            //     {title: 'Pre-fab homes', src: 'https://cdn.vuetifyjs.com/images/cards/house.jpg', flex: 3},
            //     {title: 'Favorite road trips', src: 'https://cdn.vuetifyjs.com/images/cards/road.jpg', flex: 3},
            //     {title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 3},
            // ],
        }),
        created: function () {
            console.log(this.value);
            console.log(this.lunar.solar2Lunar('197201251130'))
            console.log(this.lunar.getCanChi(this.value))
        },
        computed: {
            /**
             *  YYMMDDHH 간지 표현을 HDMYHDMY의 array로 변환한다.
             *
             * @returns {*[]}
             */
            saju: function (){
                let sajuString = this.getSajuString(); //YYMMDDHH ||YYMMDD
                console.log(sajuString)

                let sajuTmp = []
                for (let i = 0; i < 8; i++) {
                    sajuTmp[i] = sajuString[i] || ""
                }
                let sajuTmp2 = [...sajuTmp].reverse(); //HHDDMMYY

                let sajuTmp3 = []
                for (let i = 0; i < 4; i++) {
                    sajuTmp3[i] = sajuTmp2[i*2+1]
                    sajuTmp3[i+4] = sajuTmp2[i*2]
                }
                console.log(sajuTmp3)
                return sajuTmp3
            }
        },
        methods: {
            isValidDate: function (s) {
                console.log(`isValidDate - ${s}`)
                console.log(`isValidDate - ${typeof s.valueOf()}`)
                console.log(`isValidDate - ${s.length}`)
                if (s && typeof s.valueOf() === "string") {
                    // s is a string
                    console.log('isValidDate - string')

                    if (s.length == 8 || s.length == 12){
                        console.log('isValidDate - true')
                        return true;
                    }
                }
                console.log('isValidDate - false')
                return false;
            },
            getSajuString: function () {
                if (!this.isValidDate(this.value))
                    return "";

                let canchi = this.lunar.getCanChi(this.value).canchi;
                let saju = `${canchi.y}${canchi.m}${canchi.d}${canchi.t}`.replace(/\s+/g, '');

                return saju;
            },

        }

    }
</script>

<style scoped>
    .rounded-card {
        border-radius: 50px;
    }
</style>